body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    background-color: #1f1c2b;
    font-family: 'Roboto', sans-serif;
}

.wrapper {
    max-width: 960px;
    margin: 0 auto;
    overflow: hidden;
}

.portbtn {
    height: 50px;
    width: 100%;
    display: inline-flex;
    position: relative;
    margin: 10px 0 auto;
    min-width: 400px;
}

.infoBox {
    color: white;
    border-radius: 0 0 5px 5px;
    text-align: center;
    background-color: #C94362;
    position: relative;
    width: 460px;
    font-size: 15px;
}
.container {
    height: 253px;
    width: 100%;
    max-width: 460px;
    border-radius: 3px;
    background-color: #dbecec;
    float: left;
    margin-top: 10px;
    margin-right: 10px;
}

.container:nth-child(even) {
    height: 253px;
    width: 100%;
    max-width: 460px;
    border-radius: 3px;
    float: right;
    margin-right: 0;
}

button {
    height: 27px;
    width: 133px;
    border-radius: 3px;
    border: none;
    background-color: #F2CE72;
    float: left;
    color: #FFFFFF;
    font-size: 13px;
    line-height: 15px;
    display: flex;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
}

button:hover {
    background-color: #F2CE72;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}

.buttonRow {
    bottom: 15px;
    left: 15px;
    width: 100%;
    position: absolute;
}

.addStock {
    background-color: #1FBE6D;
}

.graph {
    background-color: #032B3A;
    float: left;
    margin-left: 15px;
}

.remove {
    background-color: #C94362;
    margin-left: 15px;
}

.title {
    border: none;
    width: 90%;
    margin-top: 5px;
    height: 30px;
    color: #1f1c2b;
    background: none;
    padding-top: 5px;
    float: left;
    position: absolute;
    font-size: 20px;
    font-weight: 500;
    outline: none;
}

.title:hover {
    background-color: #F2CE7260;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}

.delete {
    position: absolute;
    top: 0;
    right: 0;
    width: 20px;
    background: none;
    color: #032B3A;
    font-size: 15px;
    font-weight: 600;
}

.stockCont {
    width: 100%;
    max-height: 155px;
    min-height: 155px;
    overflow-y: auto;
    margin-top: 40px;

}



@media only screen and (max-width: 976px) {
    .container {
        width: calc(50% - 10px);
        float: left;
        min-width: 350px;
        box-sizing: border-box;
    }

    .container:nth-child(even) {
        width: 50%;
        box-sizing: border-box;
    }

    button {
        width: 30%;
    }

    .graph {
        margin-left: 5px;
    }

    .buttonRow {
        width: calc(100% - 20px);
    }

    .remove {
        margin-left: 5px;
        float: left;
        width: auto;
    }
}


@media only screen and (max-width: 768px) {
    .container {
        width: 100%;
        max-width: 100%;
        min-width: 400px;
        float: left;
        box-sizing: border-box;
    }

    .container:nth-child(even) {
        width: 100%;
        max-width: 100%;
        min-width: 400px;
        float: left;
    }

    button,  .graph, .addStock, .remove {
        width: calc(33% - 5px);
    }
}

.row {
    background-color: white;
    margin: 0;
    width: 100%;
    height: 30px;
    padding-top: 5px;
    font-size: 13px;
    vertical-align: center;
}

.row span {
    white-space: nowrap;
}

.row:nth-child(even) {
    background-color: #FBFBFB;
}
.row:last-child  {
    border-radius: 0 0 0 10px;
}
.check {
    max-width: 25px;
    margin-right: 10px;
}

.row {
    text-transform: uppercase;
}


.header {
    background-color: #1f1c2b;
    color: white;
    font-weight: 400;
    width: 100%;
    height: 30px;
    font-size: 13px;
    border-radius: 10px 0 0 0;
    text-transform: none;

}

.header div:last-child {
    max-width: 20px;
    margin-right: 10px;
}

button.save {
    background-color: #1FBE6D;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;

}

button.cancel {
    background-color: #032B3A;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
}

button.save:hover {
    background-color: #024858;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
}

button.cancel:hover {
    background-color: #0084b5;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
}


label {
    color: #1f1c2b;
    font-weight: 600;
    font-size: 13px;
}

.form-control::-webkit-input-placeholder {
   color: lightgray;
    text-transform: none;
}

.form-control::-ms-input-placeholder {
   color: lightgray;
    text-transform: none;
}

.form-control::placeholder {
   color: lightgray;
    text-transform: none;
}

.form-group:first-of-type .form-control {
    text-transform: uppercase;
}

.modal-content {
    border: none;
    box-shadow: #1f1c2b 0px 5px 25px 0;

}

.modal {
    background-color: #1f1c2b70;
}

